*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  text-align: center;
  background: rgb(240, 240, 240);
}

h1{
  color: salmon;
  margin-bottom: 20px;
}

input{
  width: 300px;
  text-align: center;
  padding: 10px;
  font-size: 1rem;
  margin-bottom: 20px;
  border-radius: 10px;
  border: 1px solid black;
}

.meals-container{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.meal-card{
  flex: 1 0 20%;
  min-width: 180px;
  max-width: 300px;
  border: 2px solid salmon;
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
}

img{
  width: 90%;
  border-radius: 10px;
}
p{
  display: -webkit-box;
  -webkit-line-clamp: 7;
  -webkit-box-orient: vertical;
  overflow: hidden;
}